import axios from 'axios'

import {API_V1_URL} from '@/config'
import numUtils from '@/helpers/numUtils'
import dateUtils from '@/helpers/dateUtils'

function apiToTransaction(data) {
  return {
    id: data.transactionId,
    type: data.type,
    date: dateUtils.formatDate(new Date(data.date)),
    c1Amount: data.currency1Amount,
    c1: data.currency1,
    c2Amount: data.currency2Amount,
    c2: data.currency2,
  }
}

function apiToPortfolio(data) {
  const totals = data.totals

  let portfolio = {
    id: data.id,
    assets: data.assets.map(asset => {
      asset.valueBGN = numUtils.roundTo(asset.valueBGN, 2)
      asset.priceBGN = numUtils.roundTo(asset.priceBGN, 2)
      asset.share = numUtils.roundTo(asset.share * 100, 2)
      return asset
    }),
    stats: {
      roi: {},
      totalValueBGN: numUtils.roundTo(totals.totalValueBGN, 2),
      totalDepositsBGN: numUtils.roundTo(totals.totalDepositsBGN, 2),
      totalWithdrawalsBGN: numUtils.roundTo(totals.totalWithdrawalsBGN, 2),
      totalCommissionsBGN: numUtils.roundTo(totals.totalCommissionsBGN, 2),
      grossProfitBGN: numUtils.roundTo(totals.totalValueBGN + totals.totalDepositsBGN - totals.totalWithdrawalsBGN + totals.totalCommissionsBGN, 2),
      netProfitBGN: numUtils.roundTo(totals.totalValueBGN + totals.totalDepositsBGN - totals.totalWithdrawalsBGN, 2),
    },
  }

  for (var periodKey in data.ROI) {
    var periodValues = data.ROI[periodKey]
    if (periodValues) {
      portfolio.stats.roi[periodKey] = {}
      for (var valueKey in periodValues) {
        portfolio.stats.roi[periodKey][valueKey] = numUtils.roundTo(periodValues[valueKey], 2)
      }
    }
  }

  return portfolio
}

export default {
  createPortfolio(userId, portfolioId) {
    const portfolio = {
      id: portfolioId,
      userId,
    }
    return axios.post(API_V1_URL + '/portfolios', portfolio, {headers: {'Content-Type': 'application/json'}})
      .then(res => res.data.data.id)
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
  getPortfolioDetails(portfolioId) {
    return axios.get(API_V1_URL + '/portfolio/' + portfolioId)
      .then(res => apiToPortfolio(res.data.data))
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
  getPortfolioTransactions(portfolioId, offset = 0, limit = 20) {
    return axios.get(API_V1_URL + '/portfolio/' + portfolioId + '/tx?offset=' + offset + '&limit=' + limit)
      .then(res => {
        return {
          transactions: res.data.data.map(apiToTransaction),
          total: res.data.count,
        }
      })
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
  getTotalPortfolios() {
    return axios.get(API_V1_URL + '/portfolio')
      .then(res => apiToPortfolio(res.data.data))
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
  deleteAllTransactions() {
    return axios.delete(API_V1_URL + '/tx')
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
}



